<template>
  <div class="params">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-alert title="注意：只允许第三极分类设置相关参数" type="warning" show-icon></el-alert>
      <el-row>
        <el-col :span="24">
          <span>选择商品分类：</span>
          <el-cascader
            v-model="selectedCategory"
            :options="categoryList"
            :props="categoryProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tabs切换 -->
      <el-tabs v-model="activeName" @tab-click="tabClick">
        <el-tab-pane label="动态参数" name="many">
          <!-- 按钮 -->
          <el-button type="primary" size="mini" :disabled="isDis" @click="addDia = true">添加参数</el-button>
          <!-- b表格 -->
          <el-table :data="manyData" style="width: 100%" border>
            <el-table-column type="expand">
              <template slot-scope="scop">
                <el-tag v-for="(item,index) in scop.row.attr_vals" :key="index" closable>{{item}}</el-tag>
                <el-input
                  v-if="scop.row.inputVisible"
                  v-model="scop.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scop.row)"
                  @blur="handleInputConfirm(scop.row)"
                ></el-input>
                <el-button v-else size="small" @click="showInput(scop.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scop">
                <!-- {{scop.row}} -->
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="alterParam(scop.row)"
                >编辑</el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteData(scop.row)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态参数" name="only">
          <el-button type="primary" size="mini" :disabled="isDis" @click="addDia = true">添加属性</el-button>
          <el-table :data="manyData" style="width: 100%" border>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scop">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="alterParam(scop.row)"
                >编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!-- 添加参数属性 -->
      <el-dialog
        :title="activeName=='many'?'添加参数':'添加属性'"
        :visible.sync="addDia"
        width="30%"
        @close="addClose"
        @closed="diaClosed('addRef')"
      >
        <el-form :model="addForm" :rules="addRul" ref="addRef">
          <el-form-item :label="activeName=='many'?'添加参数':'添加属性'" prop="name">
            <el-input v-model="addForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDia = false">取 消</el-button>
          <el-button type="primary" @click="addParams">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑参数 -->
      <el-dialog title="修改" :visible.sync="alterDia" width="30%" @close="alterClose">
        <el-form :model="alterForm" :rules="alterRul" ref="alterRef">
          <el-form-item :label="activeName=='many'?'修改参数':'修改属性'" prop="attr_name">
            <el-input v-model="alterForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="alterDia = false">取 消</el-button>
          <el-button type="primary" @click="alterPush">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedCategory: [],
      categoryList: [],
      categoryProps: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
        expandTrigger: "click"
      },
      activeName: "many",
      manyData: [],
      onlyData: [],
      addDia: false,
      addForm: {
        name: ""
      },
      addRul: {
        name: [{ required: true, message: "请输入名字", trigger: "blur" }]
      },
      alterDia: false,
      alterForm: {
        attr_id: "",
        attr_name: ""
      },
      alterRul: {
        attr_name: [{ required: true, message: "请输入名字", trigger: "blur" }]
      }
    };
  },
  methods: {
    //   获取级联列表数据
    async getCateList() {
      let { data: res } = await this.$http.get("categories");
      this.categoryList = res.data;
    },
    // 级联列表长度改变
    handleChange() {
      if (this.selectedCategory.length != 3) {
        this.selectedCategory = [];
      } else {
        this.getCateDataList();
      }
    },
    // 通过级联列表获取数据
    async getCateDataList() {
      let { data: res } = await this.$http.get(
        `categories/${this.categoryId}/attributes`,
        { params: { sel: this.activeName } }
      );
      console.log(res);
      res.data.forEach(item => {
        let arr = item.attr_vals.split(" ");
        item.attr_vals = arr;
        item.inputVisible = false;
        item.inputValue = "";
      });
      this.manyData = res.data;
    },
    // tab切换
    tabClick() {
      this.getCateDataList();
    },
    //tag显示
    showInput(row) {
      row.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 标签添加  失去焦点和回车键
    async handleInputConfirm(row) {
      if (!row.inputValue.trim()) {
        row.inputValue = "";
        row.inputVisible = false;
        return false;
      }
        if(row.inputValue.trim().length>0){
            row.attr_vals.push(row.inputValue.trim())
        }
        row.inputValue = ""
        row.inputVisible = false;
        let {data:res} = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`,{
            attr_name:row.attr_name,
            attr_sel:row.attr_sel,
            attr_vals:row.attr_vals.join(" ")
        })
        if(res.meta.status != 200){
            return this.$message.error("修改失败")
        }
        this.$message.success("修改成功")
    },
    // 弹框关闭的函数
    addClose() {
      this.$refs.addRef.resetFields();
    },
    //   添加属性
    addParams() {
      this.$refs.addRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post(
          `categories/${this.categoryId}/attributes`,
          {
            attr_name: this.addForm.name,
            attr_sel: this.activeName
          }
        );
        if (res.meta.status != 201) {
          return this.$message.error("添加失败");
        }
        this.getCateDataList();
        this.addDia = false;
        this.$message.success("添加成功");
      });
    },
    alterClose() {
      this.$refs.alterRef.resetFields();
    },
    // 修改回显
    async alterParam(row) {
      this.alterDia = true;
      let { data: res } = await this.$http.get(
        `categories/${this.categoryId}/attributes/${row.attr_id}`,
        {
          params: {
            attr_sel: row.attr_sel
          }
        }
      );
      if (res.meta.status != 200) {
        return this.$message.error("参数获取失败");
      }
      this.alterForm = res.data;
    },
    alterPush() {
      this.$refs.alterRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.put(
          `categories/${this.categoryId}/attributes/${this.alterForm.attr_id}`,
          this.alterForm
        );
        if (res.meta.status != 200) {
          return this.$message.error("修改失败");
        }
        this.alterDia = false;
        this.$message.success("修改成功");
        this.getCateDataList();
      });
    },
    async deleteData(row) {
      let { data: res } = await this.$http.delete(
        `categories/${this.categoryId}/attributes/${row.attr_id}`
      );
      if (res.meta.status != 200) {
        return this.$message.error("删除失败");
      }
      this.$message.success("删除成功");
      this.getCateDataList();
    }
  },
  //
  created: function() {
    this.getCateList();
  },

  computed: {
    isDis() {
      if (this.selectedCategory.length == 3) {
        return false;
      } else {
        return true;
      }
    },
    categoryId() {
      if (this.selectedCategory.length == 3) {
        return this.selectedCategory[2];
      } else {
        return null;
      }
    }
  }
};
</script>

<style>
.params .el-tabs .el-input {
  width: 80px;
}
.params .el-tag {
  margin: 5px 10px;
}

.params .el-alert {
  margin-bottom: 15px;
}
</style>
